header {
    width: 100%;
    height: 5rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}

.container {
    width: 100%;
    max-width: 98rem;
    margin: 0 auto;
    padding: 0 1.5rem;
}

nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.nav-brand {
    width: 2rem;
}

.menu-icons i {
    font-size: 3.5rem;
    color: #fafafa;
    cursor: pointer;
}

.close i {
    color: #eb648c;
}

.nav-list {
    width: 25rem;
    height: 100vh;
    background-color: #d7d4ed;
    position: fixed;
    top: 0;
    right: -26rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1500;
    transition: all 650ms ease-in-out;
}

.nav-list.active {
    right: 0;
}

.close {
    position: absolute;
    top: 1rem;
    left: 1.5rem;
}

.nav-item {
    margin: 1.5rem 0;
}

.nav-link {
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.7);
}

.hero {
    width: 100%;
    height: 80vh;
    background: linear-gradient(135deg, rgba(49, 24, 131, 0.9) 0%, rgba(72, 56, 149, 0.8) 100%),
        url('/assets/images/header-bg.jpg') center no-repeat;
    background-size: cover;
    position: relative;
}

.hero::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    width: 100%;
    height: 4.7rem;
    background: url('/assets/images/wave-small.png') center no-repeat;
    background-size: cover;
}

.main-message {
    width: 100%;
    max-width: 50rem;
    color: #fafafa;
    text-transform: uppercase;
    transform: translateY(20%);

    h1 {
        font-size: 4rem;
        margin: 1rem 0;
        font-family: 'Aldrich', sans-serif;
    }

    p {
        text-transform: lowercase;
    }
}

.btn {
    background-color: #eb648c;
    border-radius: 2rem;
    color: #fafafa;
    display: inline-block;
    margin-top: 2rem;
    padding: 1rem 2.5rem;
    transition: background-color 650ms;

    &:hover {
        background-color: #ce587b;
    }
}

/* experience section */

.experience-outdoors {
    padding: 0;
}

.title-heading {
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 10rem;
    color: rgba(0, 0, 0, 0.7);

    h3 {
        font-size: 1.4rem;
    }

    h1 {
        font-family: 'Aldrich', sans-serif;
        font-size: 2rem;
        margin: 0.5rem 0 1rem;
    }

    p {
        text-transform: lowercase;
    }
}

.activities-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-gap: 2.5rem;
}

.activities-grid-item {
    height: 40rem;
    padding: 5rem;
    color: #fafafa;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    z-index: 1;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(
            135deg,
            rgba(49, 24, 131, 0.9) 0%,
            rgba(72, 56, 149, 0.8) 100%
        );
        z-index: -1;
    }

    i {
        font-size: 4rem;
        // color: #eb648c;
    }

    h1 {
        font-family: 'Aldrich', sans-serif;
        font-size: 1.7rem;
        text-transform: uppercase;
        margin: 2rem 0;
    }
}

.star-gazing {
    background: url('/assets/images/stars.jpg') center no-repeat;
}

.hiking {
    background: url('/assets/images/hiking.jpg') center no-repeat;
}

.camping {
    background: url('/assets/images/camping.jpg') center no-repeat;
}

/* testimonials section */

.testimonials {
    width: 100%;
    height: 80vh;
    background: linear-gradient(135deg, rgba(49, 24, 131, 0.9) 0%, rgba(72, 56, 149, 0.8) 100%),
        url('/assets/images/testimonials-bg.jpg') center no-repeat fixed;
    background-size: cover;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        top: -3px;
        left: 0;
        width: 100%;
        height: 4.7rem;
        background: url('/assets/images/wave-small-reversed.png') center no-repeat;
        background-size: cover;
    }
}

.testimonial {
    width: 100%;
    color: #fafafa;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.testimonial-text-box {
    padding: 0 1.5rem;
    margin-bottom: 2rem;
    position: relative;

    i {
        position: absolute;
        top: -8rem;
        left: 50%;
        transform: translateX(-50%);
        font-size: 8rem;
        color: #eb648c;
        opacity: .5;
        z-index: -1;
    }
}

.testimonial-customer img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    border: 3px solid #eb648c;
}

/* adventure section */

.adventure-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-gap: 2.5rem;

    margin-bottom: 2rem;
}

.adventure-grid-item p {
    color: rgba(0, 0, 0, 0.7);
}

footer {
    padding: 2.5rem 0;
    background-color: #d7d4ed;
    text-align: center;
    color: rgba(0, 0, 0, 0.7);
}

/* media queries */

@media screen and (min-width: 350px) {
    .main-message {
        transform: translateY(40%);
    }
}

@media screen and (min-width: 470px) {
    .hero::before {
        height: 9.4rem;
        background: url('/assets/images/wave-medium.png');
        background-size: cover;
    }
    
    .testimonials::before {
        height: 9.4rem;
        background: url('/assets/images/wave-medium-reversed.png');
        background-size: cover;
    }
}

@media screen and (min-width: 700px) {
    header {
        height: 8rem;
    }

    .nav-brand {
        width: 3.5rem;
    }

    .nav-list {
        width: initial;
        height: initial;
        background-color: transparent;
        position: initial;
        top: initial;
        right: initial;
        flex-direction: row;
        transition: initial;
    }

    .menu-icons {
        display: none;
    }

    .nav-item {
        margin: 0 2.5rem;
    }

    .nav-link, .current {
        color: #fafafa;
        position: relative;
        font-size: 1.3rem;
    }

    .nav-link::before,
    .current::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -.5rem;
        background-color: #eb648c;
        width: 100%;
        height: 2px;
        transform: scaleX(0);
        transform-origin: left;
        transition: transform 650ms;
    }

    .current::before {
        transform: scaleX(1);
    }

    .nav-link:hover::before {
        transform: scaleX(1);
    }

    .hero {
        height: 60vh;
    }

    .main-message {
        transform: translateY(50%);
    }

    .title-heading h1 {
        font-size: 2.5rem;

    }
}

@media screen and (min-width: 950px) {
    .hero::before {
        height: 22.7rem;
        background: url('/assets/images/wave-large.png');
        background-size: cover;
    }
    
    .testimonials::before {
        height: 22.7rem;
        background: url('/assets/images/wave-large-reversed.png');
        background-size: cover;
    }
}

@media screen and (min-width: 1200px) {
    .hero {
        height: 75vh;
    }
}